@import reset
@import mixin

body
    +setWH(100vw,100vh)
    user-select: none
    overflow-x: hidden
    overflow-y: hidden
    +bgColor(#ffffff)

canvas.a-canvas
    +ab
    +setWH(100%, 100%)
    +setPosition("TL",0,0)

div.a-enter-vr
    +hide


// 字型
@font-face 
    font-family: 'BebasRegular'
    font-weight: normal
    src: url('../fonts/BebasRegular.woff') format("woff")
@font-face 
    font-family: 'BebasBold'
    font-weight: normal
    src: url('../fonts/BebasBold.woff') format("woff")
@font-face 
    font-family: 'NotoSansTC'
    font-weight: normal
    src: url('https://fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Regular.woff') format('woff')